const app = getApp();
import pageBottom from '@/components/page-bottom';
import uniLogin from '@/components/uni-login';
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
export default {
    components: {
        uniLogin,pageBottom,uniLoadMore
    },
    data() {
        return {
            cateMaskState: 0, //分类面板展开状态
            headerPosition: "fixed",
            headerTop: "0px",
            loadingType: "more", //加载更多状态
            filterIndex: 0,
            cateId: 0, //已选三级分类id
            priceOrder: 0, //1 价格从低到高 2价格从高到低
            cateList: [],
            storeList: [],
            goodsList: [],
			hotGoodsList:[],
			shopViewList:[],
            page: 1,
            shop: {},
			sid:0,
			searchVal:'',
			showLogin:1,
			userInfo:{
				id:0
			}
        };
    },

    onShareAppMessage(res) {
      return {
        title: this.shop.name || '快来看看这里的商品吧',
        path: '/pages/show/index?sid=' + this.shop.id 
      }
    },

    async onLoad(options) {
        this.cateId = options.tid;
		this.userInfo = uni.getStorageSync('userInfo');
		this.sid = options.sid || uni.getStorageSync('sid');
		this.sid = this.sid || 0;
		uni.setStorageSync('sid',this.sid);
		if (this.sid > 0) {
		    await app.request(this.$Url + "/clmsj/Shopview/bindShop", {
		        shopId: this.sid
		    });
		}
		console.log('sid==' + this.sid);
        this.loadCateList(options.fid, options.sid);
    },
    onShow() {
        if (this.play_media == 1) {
            this.play_media = 0;
        } else {
            this.loadData("refresh");
        }
    },
    onPageScroll(e) {
        //兼容iOS端下拉时顶部漂移
        if (e.scrollTop >= 0) {
            this.headerPosition = "fixed";
        } else {
            this.headerPosition = "absolute";
        }
    },
    //下拉刷新
    onPullDownRefresh() {
        this.loadData("refresh");
    },
    //加载更多
    onReachBottom() {
        this.loadData();
    },
	onShareAppMessage(res) {
	    // console.log("/pages/shop/index?sid=" + this.shop.id );
	    return {
	        title: this.shop.name,
	        path: "/pages/shop/index?sid=" + this.shop.id
	    };
	},
    methods: {
		//加载分类
		async loadCateList(fid, sid) {
		    const res = await app.request(
		        this.$Url + "/clmsj/goods/goodsTypeList"
		    );
		    this.cateList = res.data.data || [];
		    this.cateList.unshift({id: 0, name:'全部'})
		    this.cateId = this.cateList[0].id;
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type = "add", loading) {
		    //没有更多直接返回
		    if (type === "add") {
		        if (this.loadingType === "nomore") {
		            return;
		        }
		        this.loadingType = "loading";
		    } else {
		        this.loadingType = "more";
		    }
		
		    if (type === "refresh") {
		        this.page = 1;
		    }
		
		    uni.request({
		        url: this.$Url + "/clmsj/goods/myshop",
		        header: {
		            auth: uni.getStorageSync("session_key")
		        },
		        data: {
		            page: this.page,
					shop_id: this.sid
		        },
		        method: "POST",
		        success: res => {
		            if (res.data.code == 1) {
		                uni.navigateTo({
		                    url: "/pages/user/user"
		                });
		                return;
		            }
		
		            if (res.data.code == 0) {
		                let storeList = res.data.data.shopgoodslist;
		                this.shop = res.data.data.shop;
						uni.setNavigationBarTitle({
							title: this.shop.name
						})
						this.hotGoodsList = res.data.data.shop_main;
						this.shopViewList = res.data.data.shop_view;
		                this.page++;
						uni.setStorageSync('sid',this.shop.id);
						
		                if (type == "refresh") {
		                    this.storeList = storeList;
		                } else {
		                    this.storeList = this.storeList.concat(storeList);
		                }
		
		                //判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
		                this.loadingType =
		                    storeList.length < 10 ? "nomore" : "more";
		                if (type === "refresh") {
		                    if (loading == 1) {
		                        uni.hideLoading();
		                    } else {
		                        uni.stopPullDownRefresh();
		                    }
		                }
		            }
		        }
		    });
		},
		
		searchGoods(){
			let _this = this;
			this.page = 1;
			this.storeList = [];
			
			uni.request({
				url: this.$Url + "/clmsj/goods/myshop",
				header: {
				    auth: uni.getStorageSync("session_key")
				},
				data: {
				    page: this.page,
					shop_id: this.sid,
					keyword: this.searchVal,
					catid : this.cateId
				},
				method:'POST',
				success: (res) => {
					uni.hideLoading();
					if (res.data.code == 0) {
						this.storeList = res.data.data.shopgoodslist;
					}
				},
			})
		},
        shareShop() {},
        detailBtn(goods_id,sid) {
             uni.navigateTo({
                url: "/pages/product/product?goods_id=" + goods_id + "&sid=" + this.shop.id
            });
        },

        async saveBtn(item) {
            const that = this;
            const media_list = item.goodsphoto || [];
            uni.showLoading({
                title: "保存中..."
            });

            let count = 0;
            for (const v of media_list) {
                await this.saveFileToAlbum(v.img_url);
                count++;
                if (count == media_list.length) {
                    uni.hideLoading();
                    const res = await app.request(
                        this.$Url + "/clmsj/goods/savegoodsteam",
                        { goods_id: item.goods_id }
                    );
                    if (res.data.code == 0) {
                        that.$api.msg("保存成功！");
                    }
                }
            }
        },

        saveFileToAlbum(url) {
            return new Promise((resolve, reject) => {
                uni.downloadFile({
                    url: url.replace(/http/, "https"), //仅为示例，并非真实的资源
                    success: res => {
                        if (res.statusCode === 200) {
                            const content_type = res.header[
                                "Content-Type"
                            ].split("/")[0];
                            if (content_type == "image") {
                                uni.saveImageToPhotosAlbum({
                                    filePath: res.tempFilePath,
                                    success: function() {
                                        resolve();
                                    },
                                    fail() {
                                        reject();
                                    }
                                });
                            } else {
                                uni.saveVideoToPhotosAlbum({
                                    filePath: res.tempFilePath,
                                    success: function() {
                                        resolve();
                                    },
                                    fail() {
                                        reject();
                                    }
                                });
                            }
                        }
                    }
                });
            });
        },
        editBtn(id) {
            this.$common.edit_id = id;
            uni.navigateTo({
                url: "/pages/product/add"
            });
        },
        delBtn(id, index) {
            uni.showModal({
                title: "提示",
                content: "确定删除改商品吗？",
                success: res => {
                    if (res.confirm) {
                        uni.request({
                            url: this.$Url + "/clmsj/goods/del_goods",
                            header: {
                                auth: uni.getStorageSync("session_key")
                            },
                            data: {
                                goods_id: id
                            },
                            method: "POST",
                            success: res => {
                                if (res.data.code == 0) {
                                    this.$api.msg("删除成功！");
                                    this.storeList.splice(index, 1);
                                }
                            }
                        });
                    }
                }
            });
        },
        shareBtn(index) {
            console.log(index);
        },
        videoPlay(e) {
			let videoContextPrev = uni.createVideoContext(e.currentTarget.id)
			videoContextPrev.pause();
			return false;
            // this.play_media = 1;
            // const videoContext = uni.createVideoContext(e.currentTarget.id);
            // videoContext.requestFullScreen();
        },
        previewImg(list, index) {
            this.play_media = 1;
            let _list = [];
            list.forEach(v => {
                if (v.status == 0) {
                    _list.push(v.img_url);
                }
            });

            console.log(_list);
            uni.previewImage({
                current: list[index].img_url,
                urls: _list
            });
        },
        
        //筛选点击
        tabClick(index) {
            if (this.filterIndex === index && index !== 2) {
                return;
            }
            this.filterIndex = index;
            if (index === 2) {
                this.priceOrder = this.priceOrder === 1 ? 2 : 1;
            } else {
                this.priceOrder = 0;
            }
            uni.pageScrollTo({
                duration: 300,
                scrollTop: 0
            });
            this.loadData("refresh", 1);
            uni.showLoading({
                title: "正在加载"
            });
        },
        //显示分类面板
        toggleCateMask(type) {
            let timer = type === "show" ? 10 : 300;
            let state = type === "show" ? 1 : 0;
            this.cateMaskState = 2;
            setTimeout(() => {
                this.cateMaskState = state;
            }, timer);
        },
        //分类点击
        changeCate(item) {
            this.cateId = item.id;
            this.toggleCateMask();
            uni.pageScrollTo({
                duration: 300,
                scrollTop: 0
            });
            // this.loadData("refresh", 1);
			this.searchGoods();
            uni.showLoading({
                title: "正在加载"
            });
        },
		
        //详情
        navToDetailPage(item) {
            //测试数据没有写id，用title代替
            let id = item.store_name;
            uni.navigateTo({
                url: `/pages/product/product?id=${id}`
            });
        },
        stopPrevent() {}
    }
};
